@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {

    @include m.tablet {
        &.fijo{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            box-shadow: .1rem .2rem .1rem #333;
        }


    }

    background-color: v.$verde;

    h1{
        color: v.$blanco;
        text-align: center;
        @include m.tablet {
            margin: 0;
        }
    }

    .contenido-header {
        padding: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}

.navegacion-principal {


    @include m.tablet {
        display: flex;
        gap: 2rem;
    }
    a{
        display: block;
        text-align: center;
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;
        @include m.tablet {
            margin: 0
        }

        &:hover{
            color: v.$amarillo
        }
    }
}

